import Mockjs from 'mockjs';

// 
export const PersonLabels = {
  root: '',
  base: '',
  more: '',
  name: '',
  age: '',
  status: '',
  region: '',
  city: '',
  email: '',
  phone: '',
  visits: '',
  ip: 'IP',
  last_visit: '',
};

export interface IPerson {
  id: string;
  group: boolean;
  name: string;
  age: number;
  status: '' | '' | '';
  region: string;
  city: string;
  email: string;
  phone: number;
  visits: number;
  ip: string;
  last_visit: string;
  children?: IPerson[];
}

export function makeData(lens: number): IPerson[] {
  const mockData = {
    [`data|${lens}`]: [
      {
        id: '@id',
        name: '@cname',
        age: '@integer(18,60)',
        'status|1': ['', '', ''],
        region: '@region',
        city: '@city(true)',
        email: '@email',
        phone: '@integer(13)',
        visits: '@integer(0,1000)',
        ip: '@ip',
        last_visit: '@datetime("yyyy-MM-dd HH:mm:ss")',
        [`children|0-3`]: [
          {
            id: '@id',
            name: '@cname',
            age: '@integer(18,60)',
            'status|1': ['', '', ''],
            region: '@region',
            city: '@city(true)',
            email: '@email',
            phone: '@integer(13)',
            visits: '@integer(0,1000)',
            ip: '@ip',
            last_visit: '@datetime("yyyy-MM-dd HH:mm:ss")',
          },
        ],
      },
    ],
  };
  const result = Mockjs.mock(mockData);

  const num = Math.ceil(Math.random() * 5);

  return result.data.map((item: IPerson, index: number) => {
    if (index % num === 0) {
      const { children, ...rest } = item;
      return rest;
    }
    return item;
  });
}
