import cx from 'classnames';
import React, { forwardRef, useContext, useEffect, useMemo, useRef } from 'react';

import { checkBoxWidth, dragIconWidth, VirtualTableContext } from './consts';

interface IndeterminateCheckboxProps {
  indeterminate: boolean;
  onClick: () => void;
  className?: string;
}

const IndeterminateCheckbox = forwardRef(
  (
    {
      indeterminate = false,
      className,
      onClick,
      ...rest
    }: IndeterminateCheckboxProps &
      React.DetailedHTMLProps<React.InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>,
    ref: any
  ) => {
    const { canDragSortRow, fixedLeftCount } = useContext(VirtualTableContext);

    const defaultRef = useRef();
    const resolvedRef = ref || defaultRef;

    useEffect(() => {
      resolvedRef.current.indeterminate = indeterminate;
    }, [resolvedRef, indeterminate]);

    const leftStyle = useMemo(() => {
      if (fixedLeftCount === 0) {
        return {
          boxShadow: '1px 0 0 0 #eee',
        };
      }
      return {};
    }, [fixedLeftCount]);

    return (
      <div
        role='cell'
        className={cx(
          'sticky left-0 z-2 flex h-full items-center justify-center bg-white',
          className
        )}
        style={{
          minWidth: checkBoxWidth,
          left: canDragSortRow ? dragIconWidth : 0,
          ...leftStyle,
        }}
        onClick={(e) => {
          // 
          e.stopPropagation();
          // 
          onClick();
        }}
        onKeyPress={(e) => {
          // 
          if (e.code === 'Space') {
            // 
            e.stopPropagation();
            // 
            onClick();
          }
        }}
        onKeyDown={(e) => {
          // 
          if (e.key === 'Enter') {
            // 
            e.stopPropagation();
            // 
            onClick();
          }
        }}
      >
        <input
          type='checkbox'
          className='checkbox checkbox-sm'
          ref={resolvedRef}
          onChange={() => undefined}
          {...rest}
        />
      </div>
    );
  }
);

IndeterminateCheckbox.displayName = 'IndeterminateCheckbox';

export default IndeterminateCheckbox;
